import { getTrendingFacets } from '@algolia/recommend-core';
import { useEffect, useRef, useState } from 'react';
import { useAlgoliaAgent } from '../useAlgoliaAgent';
import { useStatus } from '../useStatus';
import { useRecommendContext, useRecommendClient } from './RecommendContext';
export function useTrendingFacets({
  indexName,
  maxRecommendations,
  recommendClient,
  threshold,
  transformItems: userTransformItems = x => x,
  facetName
}) {
  const [result, setResult] = useState({
    recommendations: []
  });
  const {
    status,
    setStatus
  } = useStatus('loading');
  const {
    hasProvider,
    register
  } = useRecommendContext();
  const {
    client,
    isContextClient
  } = useRecommendClient(recommendClient);
  useAlgoliaAgent({
    recommendClient: client
  });
  const transformItemsRef = useRef(userTransformItems);
  useEffect(() => {
    transformItemsRef.current = userTransformItems;
  }, [userTransformItems]);
  useEffect(() => {
    const param = {
      model: 'trending-facets',
      indexName,
      facetName,
      threshold,
      maxRecommendations,
      transformItems: transformItemsRef.current
    };
    if (hasProvider && isContextClient) {
      const key = JSON.stringify(param);
      return register({
        key,
        getParameters() {
          return {
            queries: [param],
            keyPair: {
              key,
              value: 1
            }
          };
        },
        onRequest() {
          setStatus('loading');
        },
        onResult(response) {
          setResult(response);
          setStatus('idle');
        }
      });
    }
    setStatus('loading');
    getTrendingFacets({
      ...param,
      recommendClient: client,
      facetName,
      transformItems: transformItemsRef.current
    }).then(response => {
      setResult(response);
      setStatus('idle');
    });
    return () => {};
  }, [indexName, maxRecommendations, client, setStatus, threshold, facetName, hasProvider, isContextClient, register]);
  return {
    ...result,
    status
  };
}